/* eslint-disable */
import react from 'react'
import { Input, View } from '@tarojs/components'
import { Dialog, Cell } from '@antmjs/vantui'

const Dialog_ = Dialog.createOnlyDialog()
export default function Demo() {
  const [value, setValue] = react.useState('')
  const alert = react.useCallback((title) => {
    Dialog_.alert({
      title: title || '',
      message: '',
    }).then((value) => {
      console.log('dialog result', value)
    })
  }, [])

  const confirm = react.useCallback(() => {
    Dialog_.confirm({
      title: '',
      message: (
        <Input
          placeholder=""
          value={value}
          onInput={(e) => setValue(e.detail.value)}
        />
      ),
    }).then((value) => {
      console.log('dialog result', value)
    })
  }, [value])

  return (
    <View>
      <Dialog_ />
      <Cell title="" onClick={() => alert('')} />
      <Cell title="" onClick={() => alert('123')} />
      <Cell title="" onClick={confirm} />
    </View>
  )
}
